    --lg-media-height: 540px;

    /*  */
    --md-description-width: 95vw;
    --md-description-height: 60vh;

    --md-media-box-width: 85vw;
    --md-media-width: 80vw;
    --md-media-height: calc(var(--md-media-width) * 9 / 16); 

    /*  */
    --sm-description-width: 90vw;
    --sm-description-height: 60vh;

    --sm-media-box-width: 95vw;
    --sm-media-width: 90vw;
    --sm-media-height: calc(var(--sm-media-width) * 9 / 16); 
    
    /* */
    
    --sp-description-width: 100vw;
    --sp-description-height: 55vh;
}

