/* page-tab transition */
.page-tab-transition-appear {
    opacity: 0;
    transform: translateY(-20px);
    z-index: 1;
}

.page-tab-transition-appear.page-tab-transition-appear-active {
    opacity: 1;
    transform: translateY(0px);

    -webkit-transition: 
        opacity 300ms linear,
        transform 600ms cubic-bezier(0.19, 1, 0.22, 1);
    transition: 
        opacity 300ms linear,
        transform 600ms cubic-bezier(0.19, 1, 0.22, 1);
}

.page-tab-transition-enter {
    opacity: 0;
    transform: translateY(-20px);
    z-index: 1;
}

.page-tab-transition-enter.page-tab-transition-enter-active {
    opacity: 1;
    transform: translateY(0px);

    -webkit-transition: 
        opacity 150ms linear,
        transform 300ms cubic-bezier(0.19, 1, 0.22, 1);
    transition: 
        opacity 150ms linear,
        transform 300ms cubic-bezier(0.19, 1, 0.22, 1);
}

.page-tab-transition-exit {
    opacity: 1;
    transform: translateY(0px);
    z-index: 0;
}

.page-tab-transition-exit.page-tab-transition-exit-active {
    opacity: 0;
    transform: translateY(0px);
}

